@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: 'Kumbh Sans', sans-serif;
    margin: 0;
    padding: 0;
    font-size: 10px;
}

.App {
    background-color: hsl(185, 75%, 39%);
    height: 100vh;
    width: 100vw;
    display: -webkit-flex;
    display: flex;
    overflow-x: hidden;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}




.main {
    background-color: rgb(255, 255, 255);
    padding: 2rem 5rem;
    border-radius: 15px;
    height: auto; 
    width: 20rem;
    /* overflow: hidden; */
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    z-index: 10;
}

.photo {
  border-radius: 50%;
  height: 10rem ;
  width: 10rem;
  -webkit-align-self: center;
          align-self: center;
  margin-bottom: 2rem;
  border: 5px solid white;
}

.text-content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    
    height: 40%; 
}

.greeting {
  font-size: 2.5rem;
  margin-bottom: 0.2rem;
}



.icon{
  height: auto;
  width: 2rem;
 
  color: blue;
  margin: 0.2rem ;
  
  
}

.icon-wrap {
  width : auto;
 
  place-self: center;
  margin-top: 1rem;

}








.button-wrapper{
    
    -webkit-justify-content: center;
    
            justify-content: center;
    margin:  auto;
    
}



.message-box{
    /* background-color: grey; */
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 1rem auto;
    text-align: center;
}

.message-box button{
    margin: 1rem 0.5rem;
    padding: 1rem 2rem;
    border-radius: 1rem;
    font-size: 1rem;
    font-weight: 600;
    background-color: white;
    color: hsl(185, 75%, 39%);;
}

.message-box button:hover{
    background-color: hsl(185, 75%, 39%);
    color:  black;
    opacity: 0.4;
}

.message-box button:focus{
    background-color: hsl(185, 75%, 39%);
   color:white ;
}
.like-box{
    /* background-color: green; */
    width: auto;
    height: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin: 0 auto;
}

.like-count{
    font-size: 2rem;
    margin: 0.5rem ;
    
}

.like-icon{
    font-size: 2rem;

}
