@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap');

body {
    font-family: 'Kumbh Sans', sans-serif;
    margin: 0;
    padding: 0;
    font-size: 10px;
}

.App {
    background-color: hsl(185, 75%, 39%);
    height: 100vh;
    width: 100vw;
    display: flex;
    overflow-x: hidden;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}




.main {
    background-color: rgb(255, 255, 255);
    padding: 2rem 5rem;
    border-radius: 15px;
    height: auto; 
    width: 20rem;
    /* overflow: hidden; */
    display: flex;
    flex-direction: column;
    z-index: 10;
}

.photo {
  border-radius: 50%;
  height: 10rem ;
  width: 10rem;
  align-self: center;
  margin-bottom: 2rem;
  border: 5px solid white;
}

.text-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    height: 40%; 
}

.greeting {
  font-size: 2.5rem;
  margin-bottom: 0.2rem;
}



.icon{
  height: auto;
  width: 2rem;
 
  color: blue;
  margin: 0.2rem ;
  
  
}

.icon-wrap {
  width : auto;
 
  place-self: center;
  margin-top: 1rem;

}







