.button-wrapper{
    
    justify-content: center;
    margin:  auto;
    
}



.message-box{
    /* background-color: grey; */
    display: flex;
    flex-direction: column;
    margin: 1rem auto;
    text-align: center;
}

.message-box button{
    margin: 1rem 0.5rem;
    padding: 1rem 2rem;
    border-radius: 1rem;
    font-size: 1rem;
    font-weight: 600;
    background-color: white;
    color: hsl(185, 75%, 39%);;
}

.message-box button:hover{
    background-color: hsl(185, 75%, 39%);
    color:  black;
    opacity: 0.4;
}

.message-box button:focus{
    background-color: hsl(185, 75%, 39%);
   color:white ;
}